
#search {
  color: red;
  width: 15vw;
  height: 5vh;
}
.page{
  height: 100%;
}

.header{

& .signupnow{
  background-color:rgb(247, 243, 243);
  text-align: center;
  margin: 0;
  font-size: 1rem;
  width: 100%;
}
&  .options {
  /*background-color: aqua;*/
  position: sticky;
}

&  .myaccount {
/*background-color: pink;*/
display: flex;
list-style: none;
justify-content: space-around;
font-weight: 700;



   &  .myaccount-links{
    color: black;

   }
   &  .logo{
    font-weight: 800;
    font-size: 1.8rem;
    color: rgba(201, 89, 44, 0.959);
   }

   &  .box{
    
   }

    
}

&  .list{
  display: flex;
list-style: none;
justify-content: space-around;
align-content: center;
font-weight: 500;
text-decoration: underline;
font-size: 18px;
margin-top: 8vh;

}
}
.hamburger-icono{
  visibility: hidden;
    }

.body{
  &  .top-body{
    display: flex;
    justify-content:center;
    height: 60vh;
    margin-top: 7vh;

    &  .top-body_text{
      background-color: whitesmoke;
      width: 100%;
      margin-left: 50px;

         .text{
          padding-left: 5vw;
          padding-top: 1vh;
          padding-right: 5vw;
          font-size: 20px;

         }

        .links{
          display: flex;
          justify-content: space-around;
          list-style: none;
          margin-top: 8vh;

            .button{
              border: 1px solid papayawhip;
              padding: 10px;
              background-color: papayawhip;
              color: black; 
              text-decoration: none; 
              /*text-transform: uppercase;*/
              font-family: 'Helvetica', sans-serif;
              border-radius: 50px;
              
            }
        }
    }

    &  .top-body_image{
      margin-right: 50px;
    
    
      & .image{
        height: 60vh;
        width: 40vw;
      }

    }
  }

  &  .containerbody{ 
    height: 100%;
    &  .containerbody-categories{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content:space-around;
      height: 100%;
      gap: 1vw;
      padding: 50px;


        &  .containerbody-categories_furniture{
          width: 23vw;
          height: 45vh;
          list-style: none;
          align-items: center;
          height: 100%;
          margin-top: 20px;
          display: flex;
          flex-direction: column;
          padding-bottom: 15px;
          border: black 1px solid;
          border-color: brown;
      

          img {
            width: 100%;
            height: 200px;
          }
        }
    }
}
