@media (max-width:576px){
  .search-mobile{
   visibility: hidden;
  }
  .myaccount{
    flex-direction: row-reverse;
  }
  .hamburger-icono{
    visibility: visible;
    color:pink;
  }
  .list{
    visibility: hidden;
  }
  .options{
    height: 10vh;
    width: 100%;
  }
  .logo{
    width: 50%;
  }
  .myaccount{
    padding-left: 6vw;
    justify-content: space-between;
    gap: 1vw;
  }
  .myaccount-links{
